import Vue from "vue";
import Index from "@/views/index.vue";
import VueRouter, { RouteConfig } from "vue-router";
Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    component: Index,
  },
  {
    path: "/error",
    name: "Erro",
    component: () => import("@/views/error.vue"),
  },
  {
    path: "/categorias",
    component: () => import("@/views/produto-frame.vue"),
    children: [
      {
        name: "Categorias",
        path: "",
        component: () => import("@/views/listagem-categorias.vue"),
      },
      {
        name: "Produtos",
        path: ":id/produtos",
        component: () => import("@/views/listagem-produtos.vue"),
      },
      {
        name: "ProdutoView",
        path: ":idCategoria/produtos/:idProduto",
        component: () => import("@/views/view-produto.vue"),
      },
    ],
  },
  {
    path: "/admin",
    component: () => import("@/views/admin/admin-frame.vue"),
    children: [
      {
        name: "login",
        path: "login",
        component: () => import("@/views/admin/login.vue"),
      },
    ],
  },
  {
    path: "*",
    name: "NotFound",
    component: () => import("@/views/error.vue"),
    props: {
      default: false,
      errorProp: { name: "404", message: "Pagina não existe" },
    },
  },
];

const router = new VueRouter({
  routes,
});

export default router;
